.section-tienda {
  padding-top: 3rem;
}
.card-mapa {
  width: 40%;
  height: 350px;
  float: right;
  margin: 1.8rem 0rem 1rem 3rem;
  box-shadow: 0px 0px 20px 0px rgba($color: #000000, $alpha: 0.25);
  border-radius: 5px;
  color: var(--primary-color);
}
@media (max-width: 991.98px) {
  .card-mapa {
    width: 50%;
  }
}
@media (max-width: 767.98px) {
  .card-mapa {
    width: 100%;
  }
}
@media (max-width: 575.98px) {
  .section-tienda {
    padding-top: .5rem;
  }  
}
@media (max-width: 524.98px) {
  .card-mapa {
    font-size: .8rem;
  }
}